'use client'

import dynamic from 'next/dynamic'
import { MaintenanceTask } from '@/types/maintenance'
import './calendar.css' // Vi vil lage denne filen for custom styling

const FullCalendar = dynamic(() => import('@fullcalendar/react'), {
  ssr: false
})

interface Props {
  tasks: MaintenanceTask[]
  onTaskDrop: (taskId: string, start: Date, end: Date) => void
}

export function MaintenanceCalendar({ tasks, onTaskDrop }: Props) {
  const events = tasks.map(task => ({
    id: task.id,
    title: task.title,
    start: task.startDate,
    end: task.endDate,
    className: `status-${task.status}`, // Legger til CSS-klasse basert pÃ¥ status
    extendedProps: { ...task }
  }))

  const handleEventDrop = (info: any) => {
    onTaskDrop(
      info.event.id,
      info.event.start,
      info.event.end || info.event.start
    )
  }

  return (
    <div className="calendar-container rounded-lg shadow-lg bg-white p-4">
      {typeof window !== 'undefined' && (
        <FullCalendar
          plugins={[
            require('@fullcalendar/daygrid').default,
            require('@fullcalendar/timegrid').default,
            require('@fullcalendar/interaction').default
          ]}
          initialView="timeGridWeek"
          headerToolbar={{
            left: 'prev,next today',
            center: 'title',
            right: 'dayGridMonth,timeGridWeek,timeGridDay'
          }}
          editable={true}
          droppable={true}
          eventDrop={handleEventDrop}
          events={events}
          locale="nb"
          slotMinTime="06:00:00"
          slotMaxTime="20:00:00"
          allDaySlot={false}
          height="100%"
          slotDuration="01:00:00"
          nowIndicator={true}
          dayMaxEvents={true}
          eventTimeFormat={{
            hour: '2-digit',
            minute: '2-digit',
            meridiem: false
          }}
          slotLabelFormat={{
            hour: '2-digit',
            minute: '2-digit',
            hour12: false
          }}
        />
      )}
    </div>
  )
} 