'use client'

import { useState } from 'react'
import { MaintenanceTask } from '@/types/maintenance'
import { NewTaskDialog } from '@/components/planlegging/NewTaskDialog'
import { MaintenanceCalendar } from '@/components/planlegging/MaintenanceCalendar'

export default function Planlegging() {
  const [tasks, setTasks] = useState<MaintenanceTask[]>([])
  const [isNewTaskOpen, setIsNewTaskOpen] = useState(false)

  const handleAddTask = (task: MaintenanceTask) => {
    setTasks(prev => [...prev, task])
    setIsNewTaskOpen(false)
  }

  const handleTaskDrop = (taskId: string, start: Date, end: Date) => {
    setTasks(prev => prev.map(task => 
      task.id === taskId 
        ? { ...task, startDate: start, endDate: end }
        : task
    ))
  }

  return (
    <div className="container mx-auto p-4">
      <div className="flex justify-between mb-4">
        <h1 className="text-2xl font-bold">Planlegging</h1>
        <button 
          onClick={() => setIsNewTaskOpen(true)}
          className="bg-primary text-white px-4 py-2 rounded"
        >
          Ny oppgave
        </button>
      </div>

      <MaintenanceCalendar 
        tasks={tasks} 
        onTaskDrop={handleTaskDrop} 
      />

      <NewTaskDialog
        open={isNewTaskOpen}
        onOpenChange={setIsNewTaskOpen}
        onAddTask={handleAddTask}
      />
    </div>
  )
} 