'use client'

import { useState } from 'react'
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { MaintenanceTask, RepeatInterval } from '@/types/maintenance'

interface Props {
  open: boolean
  onOpenChange: (open: boolean) => void
  onAddTask: (task: MaintenanceTask) => void
}

export function NewTaskDialog({ open, onOpenChange, onAddTask }: Props) {
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    customerId: '',
    customerName: '',
    address: '',
    startDate: '',
    endDate: '',
    type: 'vedlikehold' as MaintenanceTask['type'],
    repeatInterval: 'never' as RepeatInterval,
    repeatUntil: '',
    notifications: {
      customer: true,
      worker: true,
      reminderDays: 7
    }
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    
    const task: MaintenanceTask = {
      id: crypto.randomUUID(),
      ...formData,
      startDate: new Date(formData.startDate),
      endDate: new Date(formData.endDate),
      repeatUntil: formData.repeatUntil ? new Date(formData.repeatUntil) : undefined,
      status: 'planlagt'
    }
    
    onAddTask(task)
  }

  const onValueChange = (value: MaintenanceTask['type']) => {
    setFormData(prev => ({
      ...prev,
      type: value
    }))
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-2xl">
        <DialogHeader>
          <DialogTitle>Ny vedlikeholdsoppgave</DialogTitle>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="grid grid-cols-4 gap-4">
            <Select value={formData.type} onValueChange={onValueChange}>
              <SelectTrigger>
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="vedlikehold">Vedlikehold</SelectItem>
                {/* Legg til flere SelectItem-komponenter etter behov */}
              </SelectContent>
            </Select>
          </div>
          
          <Button type="submit">Lagre</Button>
        </form>
      </DialogContent>
    </Dialog>
  )
} 